import React, { PureComponent } from "react";
import PopUpImage from "./PopUpImage";
import PopUpVideo from "./PopUpVideo";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
export default class PopUpMediaSlider extends PureComponent {

  constructor(props) {
    super(props);
    this.state = {
      mediaSize: {
        height: 1000,
        width: 1000,
      },
      mediaLoaded: false,
      imgUrl: props.Post.media.image.small,
      largerHeight: 0,

      isFirst: false,
      isLast: false,
      currentIndex: 0
    };
    this.splideRef = React.createRef();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    const { Post } = nextProps
    const { ImageUrl } = nextProps;
    if (this.state.imgUrl !== ImageUrl) {
      this.setState({
        imgUrl: ImageUrl,
        mediaLoaded: false,
        mediaSize: {
          height: Post.media.height,
          width: Post.media.width
        }
      })
    }
  }

  componentDidMount() {
    const { Post } = this.props
    this.setState({
      mediaSize: {
        height: Post.media.height,
        width: Post.media.width
      }
    })

    this.splideRef.current.splide.on('moved', this.handleSlideChange);
    this.handleSlideChange(); // Check on initial load
  }

  componentWillUnmount() {
    this.splideRef.current.splide.off('moved', this.handleSlideChange);
  }

  handleSlideChange = () => {
    const splide = this.splideRef.current.splide;
    const index = splide.index;
    const isFirst = index === 0;
    const isLast = index === splide.length - 1;

    this.setState({ isFirst, isLast });
  };

  render() {
    const { Post, mediaSize, ThemeInfo, onUpdateProdImgIdx, winSize } = this.props

    const { isFirst, isLast, currentIndex } = this.state
    const settings = {
      type:'slide',
      rewind: false,
      speed: 400,
      gap: 0,
      drag: true,
      autoplay: false,
      autoHeight: true,
      padding: 0,
      gap: "1rem",
      pagination: true,
      arrows: true,
      classes: {
        pagination: 'splide__pagination tb_modal_slider_dots',
        page: 'splide__pagination__page tb_modal_slider_dot',
      },
    };
    return <>
      <PopUpImage winSize={winSize} Post={Post} mediaSize={mediaSize} ThemeInfo={ThemeInfo} mediaSizeCallback={this.mediaSize} opacity={0} />
      <div className="tb_post_modal_slide_holder_">
    
        <Splide hasTrack={false} className="tb_post_modal_media_slider" options={settings} ref={this.splideRef} onActive={(splide, prev, next) => {this.setState({ currentIndex: prev.index }, () => onUpdateProdImgIdx(this.state.currentIndex))}}>
          <SplideTrack>
            {Post.mediaList.map((Item, index) => {
              const autoPlay = currentIndex == index ? true : false
              return <SplideSlide key={index} style={{margin: 0, padding: 0}}>
  
                  {([2,4].includes(Item.type) || !autoPlay) ?
                  <PopUpImage winSize={winSize} Post={Post} Item={Item} Products={Item.product} mediaSize={mediaSize} ThemeInfo={ThemeInfo} mediaSizeCallback={this.mediaSize} hotspot opacity={1} />
                  : 
                  ([3,5].includes(Item.type)) ?
                  <>
                  <PopUpVideo winSize={winSize} Post={{ ...Post, networkId : Post.networkId, media : Item  }} Item={Item} Products={Item.products} mediaSize={mediaSize} ThemeInfo={ThemeInfo} mediaSizeCallback={this.mediaSize} opacity={1} /> 
                </> : null}
              </SplideSlide>
            })}
          </SplideTrack>
          <div className="splide__arrows splide__arrows--ltr tb_post_modal_arrow_wrapper_">
            <div className="splide__arrow splide__arrow--prev tb_post_modal_arrow tb_post_modal_arrow_left__ tb__icon tb-arrow-left-alt" disabled={isFirst} role="button" aria-label="Go to previous slide"><div></div></div>
            <div className="splide__arrow splide__arrow--next  tb_post_modal_arrow tb_post_modal_arrow_right__ tb__icon tb-arrow-right-alt" disabled={isLast} role="button" aria-label="Go to next slide"><div></div></div>
          </div>
        </Splide>
      </div>
    </>
  }
}