import React, { PureComponent } from "react";
import ReactPlayer from "react-player"
import { isTagShop, isSocialWall } from "../../../../actions/api";
import { reelPlayTimeUpdateData } from "../../../../utils/Common";

export default class PopUpVideo extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            videoLoaded: false,
            muted: true,
            played: 0
        }
        this.VideoWrap = React.createRef();
    }

    setVideoLoaded = event => {
        this.setState({
            videoLoaded: event
        })
    }

    muteToggle = () => {
        this.setState({
            muted: !this.state.muted,
        })
    }
    componentWillUnmount() {
        const { played } = this.state
        if (isTagShop) reelPlayTimeUpdateData(played)
    }
    onUpdateTime = (progress) => {
        try {
            if (isTagShop) {
                const { played } = this.state
                this.setState({
                    played: played + progress.playedSeconds
                })
            }
        }
        catch (e) {
            console.log(e)
        }

    }
    render() {    
        const { Post, mediaSize } = this.props
        const { videoLoaded, muted } = this.state
        const { media, id, link, networkId } = Post
        const noMuteIcon = [7, 3, 25]
        const muteIcon = !noMuteIcon.includes(Post.networkId) ? true : false
        const { height, width } = mediaSize
        return <div className={`tb_post_modal_video_holder${videoLoaded ? ' tb_post_video_loaded' : ''}`} ref={this.VideoWrap} key={media.video.full} >
            { muteIcon ?
                <div className="tb_m_mute_btn" onClick={this.muteToggle}>
                    <div className={`tb__icon tb-${!muted ? `unmute` : `mute`}`}><div></div></div>
                </div>
                : ''}
            <ReactPlayer
                className={`tb_post_modal_video tb_p_media-${id}`}
                url={media.video.full}
                data-height={height}
                data-width={width}
                data-type="video"
                data-network={networkId}
                data-link={link}
                data-item-id={id}
                data-load={0}
                config={{
                    facebook: {
                        appId: '117029975354072',
                        attributes: {
                            'data-width': width,
                            'data-height': height,
                            poster: media.image.small,
                            muted
                        }
                    },
                    file: {
                        attributes: {
                            preload: 'metadata',
                            poster: media.image.small,
                            muted
                        }
                    }
                }}
                loop={true}
                autoPlay={true}
                muted={muted}
                volume={1}
                playsinline={true}
                onReady={(e) => { this.setVideoLoaded(true) }}
                playing={true}
                height={'100%'}
                width={'100%'}
                controls={!muteIcon ? true : false}
                onProgress={this.onUpdateTime}
            />
            {!videoLoaded ? <div className='tb_post_modal_spinner'><div></div></div> : null}
        </div>
    }
}